import React, { Fragment, useState, useEffect, useContext } from "react";
// BS
import { Dropdown,} from "react-bootstrap";
/// Scroll
import { makeStyles } from '@material-ui/core/styles';
import PerfectScrollbar from "react-perfect-scrollbar";
//import { Link } from "react-router-dom";
import axios from "axios";
import { url as baseUrl, token } from "../../../api";
//import { Alert } from "react-bootstrap";
import {  Card,Accordion } from "react-bootstrap";
import {  Modal } from "react-bootstrap";
import "react-widgets/dist/css/react-widgets.css";
import { toast} from "react-toastify";
import {Button } from 'semantic-ui-react'
import PatientContext from "./../../context/patient/PatientContext";
import CreateUpdateMHPSS from "./../Mhpss/pages/CreateUpdateMHPSS";
import RecentActivities from "./../RecentActivities";

const RecentHistory = () => {
  const {patientObject, activeContent, saving, record, open, dispatch} = useContext(PatientContext);
  const toggle = () => dispatch({type: 'SET_OPEN', payload: !open});

  useEffect(() => {
    RecentActivities();
  }, [patientObject.personUuid]);

  const RecentActivities =()=>{
    axios
       .get(`${baseUrl}mhpss-screening/activities/patients/${patientObject.personUuid}?full=true`,
           { headers: {"Authorization" : `Bearer ${token}`} }
       )
       .then((response) => {
            dispatch({type: 'SET_RECENT_ACTIVITIES', payload: response.data});
       })
       .catch((error) => {
       });
   
  }

  return (
    <Fragment>
      {/* <Ext /> */}
     
      <div className="row">
        <div className="col-xl-3 col-xxl-3 col-lg-3">
            <RecentActivities />
        </div>
        <div className="col-xl-7 col-xxl-7 col-lg-7">
            <div className="card">
                <div className="card-header border-0  pb-2" style={{backgroundColor:"#EEEEEE"}}>
                    <h4 className="card-title">Screening</h4>
                </div>
                <div className="row">
                    <CreateUpdateMHPSS />
                </div>
            </div>
        </div>
        <div className="col-xl-2 col-xxl-2 col-lg-2">
            <RecentActivities toggle={toggle}/>
        </div>
      </div>
    <div className="col-xl-2 col-xxl-2 col-lg-2"></div>
  </Fragment>
);
};

export default RecentHistory;
